<script>
import Mapbox from "mapbox-gl";
export default {
    data() {
        return {
            fab: false,
            show: true,
            dialog: false,
            tooltips: false,
            center: [],
            tooltipsDisabled: false,
            accessToken: process.env.VUE_APP_MAPBOX_TOKEN,
            mapStyle: 'mapbox://styles/mapbox/streets-v11',
            // mapData: "https://raw.githubusercontent.com/johan/world.geo.json/master/countries.geo.json",
            latitude: 14.907233,
            longitude: -23.514992,
            zoom: 14,
            bearing: 0,
            pitch: 0,
            bgImage: "@/assets/tomb-512.png",
            DaDoPaMapa: [
              {
                geometry: {
                  type: 'marker-problema',
                  coordinates: [-23.508954, 14.920440]
                },
                properties: {
                  title: 'Mapbox',
                  description: 'Washington, D.C.'
                }
              },
              {
                geometry: {
                  type: 'marker-em-construcao',
                  coordinates: [-23.507104, 14.919133]
                },
                properties: {
                  title: 'Mapbox',
                  description: 'San Francisco, California'
                }
              },
              {
                geometry: {
                  type: 'marker-em-construcao',
                  coordinates: [-23.518408,14.910635]
                },
                properties: {
                  title: 'Mapbox',
                  description: 'San Francisco, California'
                }
              },
              {
                geometry: {
                  type: 'marker-problema',
                  coordinates: [-23.520503,14.906930]
                },
                properties: {
                  title: 'Mapbox',
                  description: 'San Francisco, California'
                }
              },
              {
                geometry: {
                  type: 'marker-resolvido',
                  coordinates: [-23.523207,14.906330]
                },
                properties: {
                  title: 'Mapbox',
                  description: 'San Francisco, California'
                }
              },
              {
                geometry: {
                  type: 'marker-problema',
                  coordinates: [-23.514992,14.907233]
                },
                properties: {
                  title: 'Mapbox',
                  description: 'San Francisco, California'
                }
              },
              {
                geometry: {
                  type: 'marker-em-construcao',
                  coordinates: [-23.516189,14.912383]
                },
                properties: {
                  title: 'Mapbox',
                  description: 'San Francisco, California'
                }
              },
              {
                geometry: {
                  type: 'marker-resolvido',
                  coordinates: [-23.525372,14.912248]
                },
                properties: {
                  title: 'Mapbox',
                  description: 'San Francisco, California'
                }
              }
            ]
        };
    },
    created() {
        // creating a non reactive map object
        this.map = null;
    },
    mounted() {
        // initialize the map
        console.log(process.env.VUE_APP_MAPBOX_TOKEN);
        this.map = new Mapbox.Map({
            accessToken: this.accessToken,
            container: this.$refs.map,
            style: this.mapStyle ,
            center: [this.longitude, this.latitude],
            zoom: this.zoom,
            pitch: this.pitch,
            bearing: this.bearing,
        });
        this.map.addControl(new Mapbox.NavigationControl());
        let self = this;
        this.DaDoPaMapa.forEach(function(marker) {

          // create a HTML element for each feature
          var markerEl = document.createElement('div');
          markerEl.className = marker.geometry.type;
          console.log("CORD: "+marker.geometry.coordinates)
          // make a marker for each feature and add to the map
          new Mapbox.Marker(markerEl)
            .setPopup(new Mapbox.Popup({ offset: 25 })
            // .setHTML('<h3>'+marker.properties.title+'</h3><p>'+marker.properties.description+'</p>'))
            .setHTML('<h3>'+marker.properties.title+'</h3><v-chip class="ma-2" color="orange" text-color="white">Premium<v-icon right>mdi-star</v-icon></v-chip><p>'+marker.properties.description+'</p>'))
            .setLngLat(marker.geometry.coordinates)
            .addTo(self.map);
            markerEl.addEventListener('click', () => {
                self.map.flyTo({
                    center: marker.geometry.coordinates,
                    zoom: 17,
                });
            });
        });
          // console.log(this.longitude+', '+this.latitude);
        /* this.map.addControl(
            new Mapbox.GeolocateControl({
                positionOptions: {
                    enableHighAccuracy: true
                },
                trackUserLocation: true
            })
        ); */
        if (navigator.geolocation) {
          navigator.geolocation.getCurrentPosition(displayLocationInfo);
        }
        function displayLocationInfo(position) {
          const lng = position.coords.longitude;
          const lat = position.coords.latitude;
          self.map.flyTo({
              center: [lng, lat],
              zoom: 14,
          });
          console.log(`longitude: ${ lng } | latitude: ${ lat }`);
        }

    },

  watch: {
    fab (val) {
      this.tooltips = false
      this.tooltipsDisabled = false
      val && setTimeout(() => {
        this.tooltips = true
        this.$nextTick(() => this.tooltipsDisabled = true)
      }, 250)
    },
  }
}
</script>

